*, *::before, *:after {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    background-color:skyblue;
}

.App {
    width:85%;
    margin: auto;
}

img {
    height: 50px;
}

.rooms {
    padding-top:70px;
    display:grid;
    grid-template-columns:repeat(3, 1fr);
    gap:20px;
}

.room {
    min-height:250px;
    background-color:rgb(150, 198, 218);
    border: 2px solid rgb(61, 163, 181);
    box-shadow:2px 5px 10px 3px rgba(13, 0, 43, 0.3);
}

.room-cont {
    padding:20px;
}

.room-cont h5, p {
    margin:0;
    font-size:.8em;
}

.occupant {
    background-color:white;
    border-radius:10px;
    margin-bottom:10px;
}




/*WORKERS WORKERS WORKERS WORKERS WPORKERS WORKERS WORKERS WORKERS*/

.work-head {
padding-top:100px;
}

.work-head h3 {
    margin-left:20px;
    font-size:1.8em;
}

.work-head img {
    margin-top:20px;
}

.workers {
    margin-top: 10px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
    text-align: left;
}
.work-head {
    display: flex;
    flex:start;
}
.person {
    background-color:rgb(247, 248, 227);
    border-radius:1em;
    margin-bottom:20px;
    box-shadow:2px 3px 4px 5px rgba(0, 0, 0, 0.2);
}

.person-cont{
    padding:15px 20px;
}


.person-name {
    display:flex;
    flex:start;
    font-size:.9em;
}
.person img {
    border:1px solid rgb(69, 190, 211) ;
    border-radius:5em;
}

.person-name h4 {
    margin:0px;
    padding:5px 0px 0px 5px;
}

.person p {
    font-weight:bold;
    font-size:.9em;
    margin:0;
    padding:2px 0px
}

.person select {
    border-radius:5px;
    padding:5px 0px;
    border:1px solid rgb(69, 190, 211) ;
    outline:none;
}

.person option {
    background-color:rgb(130, 192, 202);
    box-shadow:2px 2px 10px 5px rgba(0, 0, 0, 0.1);
    border:none;
}
.person-btn {
    margin:10px 0px;
    display:flex;
    flex:start;
}

.person button {
    margin-left:8%;
    border-radius:5px;
    padding:0px 10px;
    border:1px solid rgb(69, 190, 211) ;
}

.person button:hover,
.person select:hover {
    background-color:rgb(190, 213, 216);
}



































@media(max-width:960px) {
    .workers {
        grid-template-columns:repeat(2, 1fr);
    }
}